const TIMER = 'ContentWall.timer';
const DISCONNECTED = 'ContentWall.disconnected';

class ContentWall {
  constructor() {
    this.timer = sdk.storage.get(TIMER) ? sdk.storage.get(TIMER) : 0;
    this.limit = sdk.storage.get(DISCONNECTED) ? 30 : 120;
  }

  handleRequest(request) {
    return new Promise(resolve => {
      switch (request.method) {
        case 'shouldShow':
          return resolve(this.shouldShow(request));
        case 'increase':
          return resolve(this.increase());
        case 'disconnect':
          return resolve(this.disconnect(request));
        case 'open':
          return resolve(this.openPopUp(request));
      }
    });
  }

  shouldShow(request) {
    if (!request.message?.domain || DOMAINS.indexOf(request.message.domain) === -1 || sdk.user.status.elite)
      return false;

    const domain = request.message.domain;

    if (
      !sdk.proxy.bypass.isInList(domain) &&
      (sdk.proxy.secured.isInList(domain) || sdk.proxy.status.status === 'connected')
    ) {
      sdk.reporting.report('contentWall', 'show', domain);
      return { limit: this.limit, timer: this.timer };
    }

    return false;
  }

  disconnect(request) {
    if (!request.message?.domain || DOMAINS.indexOf(request.message.domain) === -1) return false;

    const domain = request.message.domain;

    if (sdk.proxy.secured.isInList(domain)) {
      sdk.proxy.secured.remove(domain);
    }

    this.timer = 0;
    this.limit = 30;

    sdk.storage.set(DISCONNECTED, true);
    sdk.storage.set(TIMER, this.timer);

    sdk.reporting.report('contentWall', 'disconnect', domain);
    sdk.reporting.report('bypass', 'addCw', domain).then(() => {
      sdk.proxy.bypass.add(domain);
    });
  }

  increase() {
    sdk.storage.set(TIMER, ++this.timer);
  }

  openPopUp(request) {
    sdk.reporting.report('contentWall', 'popup', sdk.tabs.active.domain);
    sdk.user.openPopUp('contentWall', request.message && request.message.domain ? request.message.domain : undefined);
  }
}

export default ContentWall;
