class WebRTC {
  constructor() {
    browser.privacy.network.webRTCIPHandlingPolicy.get({}, response => {
      if (response && response.value === 'disable_non_proxied_udp') this.status = 'on';
      else this.status = 'off';
    });
  }

  getStatus = () => this.status;

  setStatus = status => {
    if (['on', 'off', 'disabled'].indexOf(status) === -1) return;
    if (this.status === status) {
      return;
    }
    this.status = status;
    if (this.status === 'on') return this.enable();
    return this.disable();
  };

  enable = () => {
    return new Promise(resolve => {
      browser.privacy.network.webRTCIPHandlingPolicy.set({ value: 'disable_non_proxied_udp' }, () => {
        if (sdk.config.browser === 'Firefox') {
          browser.privacy.network.peerConnectionEnabled.set({ value: false });
        }
        return resolve();
      });
    });
  };

  disable = () => {
    return new Promise(resolve => {
      browser.privacy.network.webRTCIPHandlingPolicy.set({ value: 'default_public_and_private_interfaces' }, () => {
        if (sdk.config.browser === 'Firefox') {
          browser.privacy.network.peerConnectionEnabled.set({ value: true });
        }
        return resolve();
      });
    });
  };

  static inject() {
    const webRTC = new WebRTC();
    sdk.webRTC = webRTC;
    return webRTC;
  }
}

export default WebRTC;
