import sdk from '@common-shared/SDK';
import defaultConfig from 'shared/config/servers';
import { isLooksLikeDomain } from '@common-shared/Utils/DomainUtils';
import isNumber from 'lodash/isNumber';
import Network from '../Wdgt/Network';
import HssConfig from './HssConfig';

const CONFIG = 'Proxy.Config.config';
const STOP_UPDATE = 'Proxy.Config.stopUpdate';

class Config {
  constructor() {
    this.config = {
      servers: {
        elite: {},
        regular: {}
      },
      locations: {
        elite: [],
        regular: []
      }
    };

    if (sdk.config.infra !== 'hss') {
      this.restoreConfig();
    }
    this.runPolling();

    sdk.forceLoadSD = this.loadConfig;

    if (sdk.config.debug) {
      sdk.restoreConfig = this.restoreConfig;
    }
  }

  runPolling() {
    this.loadConfig();

    setInterval(() => {
      this.loadConfig();
    }, 24 * 60 * 60 * 1000);
  }

  loadConfig = () => {
    if (sdk.storage.get(STOP_UPDATE)) return;

    const manualConfigs = sdk.storage.get('manual-configs');
    if (manualConfigs) {
      console.log('use manual configs', manualConfigs);
      this.config = manualConfigs;
      return;
    }

    if (sdk.config.infra && sdk.config.infra === 'hss') {
      return HssConfig.loadConfig().then(response => {
        this.config = response;
      });
    }

    const loadConfigs = (urls, i, results) => {
      i = typeof i === 'undefined' ? 0 : ++i;
      results = typeof results === 'undefined' ? [] : results;

      return new Promise(resolve => {
        if (i === urls.length || !urls[i]) return resolve(results);

        Network.get(urls[i])
          .then(result => {
            try {
              result = JSON.parse(result);
              if (result.servers) {
                results.push(result.servers);
              } else {
                result.push([]);
              }
            } catch (e) {
              result.push([]);
            }

            return resolve(loadConfigs(urls, i, results));
          })
          .catch(e => {
            results.push([]);
            return resolve(loadConfigs(urls, i, results));
          });
      });
    };

    loadConfigs([sdk.config.configs.free, sdk.config.configs.elite]).then(configs => {
      const temp = {
        free: undefined,
        elite: undefined
      };

      if (configs && configs[0] && configs[0].length > 0) {
        temp.free = configs[0];
      } else if (defaultConfig.free) {
        temp.free = defaultConfig.free;
      }

      if (configs && configs[1] && configs[1].length > 0) {
        temp.elite = configs[1];
      } else if (defaultConfig.elite) {
        temp.elite = defaultConfig.elite;
      }

      this.parseConfig(temp);
    });
  };

  restoreConfig = async () => {
    let config = sdk.storage.get(CONFIG) ? sdk.storage.get(CONFIG) : defaultConfig;
    if (typeof config === 'string') config = defaultConfig;
    this.parseConfig(config);
  };

  parseConfig = data => {
    const config = {
      servers: {
        elite: {},
        regular: {}
      },
      locations: {
        elite: [],
        regular: []
      }
    };

    for (const k in data.free) {
      const server = data.free[k];
      if (!this.isValidServerConfig(server)) continue;
      if (data.elite && ['us', 'gb', 'fr', 'in', 'sg'].indexOf(server.country) !== -1) continue;

      if (config.locations.regular.indexOf(server.country) === -1) {
        config.locations.regular.push(server.country);
        config.servers.regular[server.country] = [];
      }

      config.servers.regular[server.country].push(server);
    }

    for (const k in data.elite) {
      const server = data.elite[k];
      if (!this.isValidServerConfig(server)) continue;

      if (config.locations.elite.indexOf(server.country) === -1) {
        config.locations.elite.push(server.country);
        config.servers.elite[server.country] = [];
      }

      config.servers.elite[server.country].push(server);
    }

    sdk.storage.set(CONFIG, data);

    this.config = config;
  };

  isValidServerConfig = ({ address, scheme, port }) => {
    if (!isLooksLikeDomain(address)) return false;
    if (!/^https?$/.test(scheme)) return false;
    if (!isNumber(port)) return false;
    if (port < 0 || port > 65353) return false;
    return true;
  };

  getLocations = type => {
    type = type || 'regular';

    if (type === 'all') {
      return this.config.locations;
    }

    if (!this.config.locations[type]) {
      return {};
    }

    return this.config.locations[type];
  };

  getServerByAddress = address => {
    // iterate over keys - regular and elite
    for (const type of Object.values(this.config.servers)) {
      if (!type) {
        continue;
      }
      // iterate over country keys - get server list
      for (const servers of Object.values(type)) {
        if (!servers) {
          continue;
        }
        const server = servers.find(s => s.address === address);
        if (!server) {
          continue;
        }
        return server;
      }
    }
    return null;
  };

  disableServer = address => {
    const server = this.getServerByAddress(address);
    if (!server) {
      return false;
    }
    if (server.disable) {
      return false;
    }
    server.disable = new Date();
    return true;
  };

  getServers = (type, country) => {
    type = type || 'regular';

    if (type === 'all') return JSON.parse(JSON.stringify(this.config.servers));

    const servers = JSON.parse(JSON.stringify(this.config.servers));

    if (!servers[type]) return null;
    if (country && !servers[type][country]) return null;
    if (country && servers[type][country]) return servers[type][country].filter(s => !s.disable);
    if (!country && servers[type]) return servers[type];

    return null;
  };
}

export default Config;
