import sdk from '@common-shared/SDK';
import objectMerge from 'object-merge';
import Network from '../Wdgt/Network';

const URL = 'https://www.google-analytics.com/collect';
const PARAMETERS_MAP = {
  version: 'v',
  clientId: 'cid',
  userId: 'uid',
  trackingId: 'tid',
  hitType: 't',
  applicationName: 'an',
  applicationId: 'aid',
  applicationVersion: 'av',
  contentDescription: 'cd',
  category: 'ec',
  action: 'ea',
  label: 'el',
  value: 'ev',
  screenName: 'cd',
  campaignName: 'cn',
  campaignSource: 'cs',
  campaignMedium: 'cm',
  clientIpAddress: 'uip',
  userAgent: 'ua'
};

const PARAMS = 'Reporting.Analytics.param';

class Analytics {
  init() {
    if (!sdk?.config?.reporting?.analytics?.id)
      return new Promise(resolve => {
        resolve();
      });

    return this.restoreParams();
  }

  event(params) {
    return new Promise(resolve => {
      if (!params?.category || !sdk?.config?.reporting?.analytics?.id) {
        return resolve();
      }

      const config = sdk.config.reporting.analytics;
      const rand = Number(
        Date.now()
          .toString()
          .match(/[0-9]{2}$/)
      );
      const sampling = config.sampling[params.category] ? config.sampling[params.category] : config.sampling.default;

      if (rand > sampling - 1) return resolve();

      if (!config.noPrefix) {
        params.category = `_${params.category}`;
      }
      params.hitType = 'event';
      params.version = '1';
      params.clientId = sdk.user.hash;
      params.applicationId = sdk.config.application.id;
      params.applicationName = sdk.config.application.name;
      params.applicationVersion = sdk.config.application.version;
      params = objectMerge(params, this.params);

      return resolve(this.send(params));
    });
  }

  send(params) {
    return new Promise((resolve, reject) => {
      const stringify = parameters => {
        const shortParameters = {};
        for (const name in parameters) {
          const value = parameters[name];
          if (typeof value === 'undefined' || value == null) {
            continue;
          }

          shortParameters[PARAMETERS_MAP[name] || name] = value;
        }

        const bodyParts = [];
        for (const key in shortParameters) {
          bodyParts.push(`${key}=${encodeURIComponent(shortParameters[key])}`);
        }

        return bodyParts.join('&');
      };

      Network.post(URL, stringify(params))
        .then(() => {
          return resolve();
        })
        .catch(() => {
          return resolve();
        });
    });
  }

  restoreParams() {
    this.params = sdk.storage.get(PARAMS) ? sdk.storage.get(PARAMS) : {};

    return new Promise(resolve => {
      if (!this.params.trackingId) {
        return this.createParams().then(() => {
          return resolve();
        });
      }
      return resolve();
    });
  }

  createParams() {
    const params = {
      trackingId: sdk.config.reporting.analytics.id
    };

    return new Promise(resolve => {
      this.updateParams(params).then(() => {
        browser.tabs.query(
          {
            url: `https://chrome.google.com/*${sdk.config.application.applicationId}*`
          },
          tabs => {
            const tab = tabs[0];
            if (!tab) {
              return resolve();
            }

            const campaignName = tab.url.match(/utm_campaign=([^&]+)/);
            const campaignSource = tab.url.match(/utm_source=([^&]+)/);
            const campaignMedium = tab.url.match(/utm_medium=([^&]+)/);

            const params = {
              campaignName: campaignName && campaignName[1] ? campaignName[1] : null,
              campaignSource: campaignSource && campaignSource[1] ? campaignSource[1] : null,
              campaignMedium: campaignMedium && campaignMedium[1] ? campaignMedium[1] : null
            };

            this.updateParams(params).then(() => {
              return resolve();
            });
          }
        );
      });
    });
  }

  updateParams(params) {
    this.params = objectMerge(this.params, params);

    return sdk.storage.set(PARAMS, this.params);
  }
}

export default Analytics;
