import sdk from '@common-shared/SDK';
import { disableOtherProxyExtensions } from '@common-shared/Utils/ExtensionManagment';
import { asyncTimeout } from '@common-shared/Utils/AsyncTimeout';
import Timer from './Timer';
import Status from './Status';
import Authorization from './Authorization';

import BypassWebsites from './BypassWebsites';
import SecuredWebsites from './SecuredWebsites';

const STORAGE_ERROR = 'Storage.error';
const GDPR_CONNECTED = 'GDPR.Connected';

class Proxy extends Status {
  constructor() {
    super();

    sdk.proxy = {
      status: this.status,
      render: this.render,
      connect: this.connect,
      reconnect: this.reconnect,
      disconnect: this.disconnect,
      updateProxy: this.updateProxy,

      getServers: this.servers.config.getServers,
      getLocations: this.servers.config.getLocations,
      getGlobalStatus: this.getGlobalStatus,
      getActiveServers: this.servers.getActive,
      getServerByAddress: this.servers.config.getServerByAddress,
      disableOtherProxy: disableOtherProxyExtensions,

      onProxyError: this.onProxyError,
      onStatusChanged: this.onStatusChanged,

      disableErrors: false
    };

    const timer = new Timer();
    sdk.proxy.timer = timer.get.bind(timer);

    const bypass = new BypassWebsites();
    sdk.proxy.bypass = {
      add: bypass.add.bind(bypass),
      get: bypass.get.bind(bypass),
      remove: bypass.remove.bind(bypass),
      isInList: bypass.isInList.bind(bypass),
      onChange: bypass.onChange
    };

    this.SecuredWebsites();

    this.initDone();
  }

  SecuredWebsites(alexa) {
    const secured = new SecuredWebsites(alexa);
    sdk.proxy.secured = {
      add: secured.add.bind(secured),
      get: secured.get.bind(secured),
      remove: secured.remove.bind(secured),
      isInList: secured.isInList.bind(secured),
      isLocked: secured.isLocked.bind(secured),
      unlock: secured.unlock.bind(secured),
      onChange: secured.onChange
    };
  }

  async initDone() {
    new Authorization();
    if (sdk.storage.get(STORAGE_ERROR)) {
      await this.renderClearProxy();
    } else {
      await this.updateStatus('disconnected');
      if (sdk.config.webRTC?.auto) {
        await sdk.webRTC.setStatus('off');
      }
      await this.updateProxy();
      this.onStatusChanged.dispatch(this.status);
    }
  }

  connect = async (location, delay) => {
    location = location || 'us';
    debug(`Connect: ${location}`);

    this.onStatusChanged.dispatch({ status: 'connecting', location: this.status.location });

    await sdk.storage.set(GDPR_CONNECTED, true);
    await this.updateStatus('connected', location);
    if (sdk.config.webRTC?.auto) {
      await sdk.webRTC.setStatus('on');
    }
    await sdk.reporting.onBeforeConnectionStartPreparation();
    await this.updateProxy();
    sdk.reporting.onAfterConnectionStartReport({ location });
    await asyncTimeout(delay || sdk.config.proxy.connecting.delay * 1000);
    this.onStatusChanged.dispatch(this.status);
  };

  reconnect = async location => {
    location = location || 'us';
    debug(`Reconnect: ${location}`);

    await sdk.reporting.onAfterConnectionEndReport();
    await this.updateStatus('connected', location);
    if (sdk.config.webRTC?.auto) {
      await sdk.webRTC.setStatus('on');
    }
    await sdk.reporting.onBeforeConnectionStartPreparation();
    await this.updateProxy();
    sdk.reporting.onAfterConnectionStartReport({ location, reconnect: true });
    this.onStatusChanged.dispatch({ status: 'connected', location: this.status.location });
  };

  disconnect = async ({ delay, cancel = false, error } = {}) => {
    debug('Disconnect');

    this.onStatusChanged.dispatch({ status: 'disconnecting', location: this.status.location });
    await this.updateStatus('disconnected');
    if (sdk.config.webRTC?.auto) {
      await sdk.webRTC.setStatus('off');
    }
    await this.updateProxy();
    sdk.reporting.onAfterConnectionEndReport({ error, cancel });
    await asyncTimeout(delay || sdk.config.proxy.disconnecting.delay * 1000);
    this.onStatusChanged.dispatch(this.status);
  };
}

export default Proxy;
