import sdk from '@common-shared/SDK/';

const I18N_TIMESTAMP = 'i18n.timestamp';
const I18N_DATA = 'i18n.data';

export default class Inter {
  constructor() {
    this.data = null;
    this.updateDelay = 1000 * 60 * 60 * 4; // 4 hours
    this.updateInterval = null;
    this.url = 'https://api.lokalise.com/api2/projects/%PROJECT%/keys/?limit=1000&include_translations=1';
    this.prepareSDK();
  }

  async init() {
    if (this.updateInterval) {
      clearTimeout(this.updateInterval);
      this.updateInterval = null;
    }
    const timestamp = sdk.storage.get(I18N_TIMESTAMP);
    if (!timestamp || timestamp < Date.now()) {
      await this.getTranslations();
    } else {
      this.data = sdk.storage.get(I18N_DATA);
    }
    if (!this.getAllLanguages().length) {
      return;
    }
    this.updateInterval = setTimeout(() => {
      this.init();
    }, this.updateDelay);
  }

  getAllLanguages() {
    if (this.data) {
      return Object.keys(this.data);
    }

    return [];
  }

  prepareSDK() {
    sdk.lokalise = {
      get: this.get.bind(this),
      getAllLanguages: this.getAllLanguages.bind(this),
      fetch: this.getTranslations.bind(this)
    };
  }

  getTranslations() {
    const { lokalise } = sdk.config;
    if (!lokalise) return false;
    const projectId = lokalise.id;
    const token = lokalise.token;
    if (!projectId || !token) {
      return false;
    }
    return fetch(this.url.replace('%PROJECT%', projectId), {
      headers: {
        'x-api-token': token
      }
    })
      .then(r => r.json())
      .then(r => this.parse(r))
      .then(() => {
        this.save();
      })
      .catch(err => console.log(err));
  }

  save() {
    sdk.storage.set(I18N_DATA, this.data);
    sdk.storage.set(I18N_TIMESTAMP, Date.now() + this.updateDelay);
  }

  parse(data) {
    if (!data.keys) return false;
    const { keys } = data;
    const formattedTranslations = {};
    keys.forEach(key => {
      const { translations } = key;
      translations.forEach(tran => {
        if (!formattedTranslations[tran.language_iso]) {
          formattedTranslations[tran.language_iso] = {};
        }
        formattedTranslations[tran.language_iso][key.key_name.web] = tran.translation;
      });
    });
    this.data = formattedTranslations;
    return true;
  }

  get(key) {
    try {
      const lang = sdk.storage.get('Locales.language') || 'en';
      const words = this.data[lang];

      let word = words[key];

      if (!word) {
        word = this.data.en[key];
      }

      return word.replace(/%BROWSER%/, sdk.config.browser);
    } catch (e) {
      console.log(e);
    }
  }
}
