import sdk from '@common-shared/SDK/';

const STATUS = 'Connection.Cookie.status';
const COUNTER = 'Connection.Cookie.counter';

class CookiesBlocker {
  constructor() {
    this.status = sdk.storage.get(STATUS) ? sdk.storage.get(STATUS) : 'off';
    this.counter = sdk.storage.get(COUNTER) ? sdk.storage.get(COUNTER) : 0;

    setInterval(() => {
      sdk.storage.set(COUNTER, this.counter);
    }, 30000);
  }

  getCounter() {
    return this.counter;
  }

  getStatus() {
    return this.status;
  }

  setStatus(status) {
    if (['on', 'off', 'disabled'].indexOf(status) === -1) return;
    this.status = status;
    return sdk.storage.set(STATUS, status);
  }

  check(headers) {
    if (this.status !== 'on') return headers;

    for (const k in headers) {
      let name = {};
      name = headers[k].name.toLowerCase();

      if (name === 'cookie') {
        if (
          Number(
            Date.now()
              .toString()
              .match(/[0-9]{2}$/)
          ) < 10
        )
          this.counter++;
        headers.splice(k, 1);
        break;
      }
    }

    return headers;
  }
}

export default CookiesBlocker;
