import sdk from '@common-shared/SDK/';

const DAILY_LIMIT = 250;
const ADDITIONAL_FOR_LOGGED_USERS = 250;
const ADDITIONAL_FOR_DOWNLOADED_APP_USERS = 500;

class LimitUsage {
  constructor() {
    this.dailyLimit = DAILY_LIMIT;
  }

  getLimit() {
    const { login } = sdk.user.status;

    if (login) {
      this.dailyLimit = DAILY_LIMIT + ADDITIONAL_FOR_LOGGED_USERS;
    }

    if (sdk.storage.get('appDownloaded')) {
      this.dailyLimit = DAILY_LIMIT + ADDITIONAL_FOR_LOGGED_USERS + ADDITIONAL_FOR_DOWNLOADED_APP_USERS;
    }

    return this.dailyLimit;
  }
}

export default LimitUsage;
