// Source for regex - https://www.oreilly.com/library/view/regular-expressions-cookbook/9781449327453/ch08s15.html
// Regular Expressions Cookbook, 2nd Edition # 8.15. Validating Domain Names
export const isLooksLikeDomain = string => !!/^([a-z0-9]+(-[a-z0-9]+)*\.)+[a-z]{2,}$/i.test(string);

export const parseUrlDomain = url => {
  if (!url) return undefined;

  const parsed = url.toLowerCase().match(/^[^:]+:\/\/(www\.)?([^?/\\]+)/);
  if (!parsed || !parsed[2]) return undefined;

  return parsed[2];
};
