import sdk from '@common-shared/SDK';
import 'config';
import Debug from '@common-shared/Wdgt/Debug';

import User from '@common-shared/User';
import Inter from '@common-shared/Internatiolization';
import Tabs from '@common-shared/Wdgt/Tabs';
import Proxy from '@common-shared/Proxy';
import Storage from '@common-shared/Wdgt/Storage';
import Reporting from '@common-shared/Reporting';
import Monitoring from '@common-shared/Monitoring';
import RequestMonitoring from '@common-shared/RequestMonitoring';
import Messaging from './Messaging';
import IconController from './IconController';
import DesktopMessages from './DesktopMessages';

class background {
  constructor() {
    this.init();
  }

  init() {
    console.log('init');
    new Debug();
    new Storage().init().then(() => {
      new Tabs().init().then(() => {
        new User().init().then(() => {
          new Reporting().init().then(() => {
            new Proxy();
            new Inter().init();
            new Messaging();
            new Monitoring();
            new IconController();
            new DesktopMessages();
            new RequestMonitoring({ sword: true });
            sdk.loading.loaded = true;
          });
        });
      });
    });
  }
}

try {
  new background();
} catch (e) {
  console.log('CRITICAL ERROR');
  console.log(e);
}
