/* eslint-disable */
import sdk from '@common-shared/SDK';
import Internal from './Internal';
import Analytics from './Analytics';
import ConnectionReport from './ConnectionReport';
import { isFirefox } from "@common-shared/BrowserDetection";
import { DATA_CONSENT_ACCEPTED } from "@common-shared/Utils/StorageKeys";

class Reporting {
  init() {
    this.internalObj = new Internal();
    this.analyticsObj = new Analytics();
    this.connectionReportObj = new ConnectionReport();

    return this.analyticsObj.init().then(() => {
      sdk.reporting = {
        report: this.report.bind(this),
        internal: this.internal.bind(this),
        analytics: this.analytics.bind(this),
        onAfterConnectionStartReport: this.connectionReportObj.onAfterConnectionStartReport,
        onBeforeConnectionStartPreparation: this.connectionReportObj.onBeforeConnectionStartPreparation,
        onAfterConnectionEndReport: this.connectionReportObj.onAfterConnectionEndReport
      };
    });
  }

  report(...params) {
    if (!isFirefox || sdk.storage.get(DATA_CONSENT_ACCEPTED)) {
      this.internal.apply(this, params);
      return this.analytics.apply(this, params);
    }
    return Promise.resolve();
  }

  internal(...params) {
    if (params.length === 0) {
      return new Promise(resolve => {
        resolve();
      });
    }

    const fields = ['event', 'category', 'action', 'label', 'value'];
    const record = {};

    for (const k in params) {
      if (typeof params[k] !== 'object' && fields[k]) {
        record[fields[k]] = params[k];
        continue;
      }

      if (typeof params[k] === 'object') {
        for (const j in params[k]) {
          if (!params[k][j]) continue;
          record[j] = params[k][j];
        }
      }
    }

    if (!record.event) {
      return new Promise(resolve => {
        resolve();
      });
    }
    const report = {
      event: record.event,
      payload: {}
    };

    delete record.event;
    for (const k in record) {
      report.payload[k] = record[k];
    }

    return this.internalObj.event(report);
  }

  analytics(...params) {
    if (params.length === 0) {
      return new Promise(resolve => {
        resolve();
      });
    }
    const fields = ['category', 'action', 'label', 'value'];
    const record = {};

    for (const k in params) {
      if (typeof params[k] !== 'object' && fields[k]) {
        record[fields[k]] = params[k];
        continue;
      }

      if (typeof params[k] === 'object') {
        for (const j in params[k]) {
          record[j] = params[k][j];
        }
      }
    }

    if (!record.category) {
      return new Promise(resolve => {
        resolve();
      });
    }

    if (record.value) {
      record.label = `${record.label}_${record.value}`;
    }

    return this.analyticsObj.event({
      category: record.category,
      action: record.action,
      label: record.label
    });
  }
}

export default Reporting;
