import sdk from '@common-shared/SDK/';

const URL = `${sdk.config.protocolPrefix}://${sdk.config.application.applicationId}/static/data/sword`;

class Rules {
  constructor() {
    this.rules = {};
  }

  async loadRules() {
    const result = await fetch(URL);
    const data = await result.json();
    const rules = {};
    for (const k in data) {
      try {
        if (data[k].match(/\.$/)) continue;
        const rule = data[k].split('.');
        rule.reverse();
        rules[rule[0]] = rules[rule[0]] ? rules[rule[0]] : [];
        rules[rule[0]].push(data[k]);
      } catch (e) {
        error(e);
      }
    }
    this.rules = rules;
  }

  isInList(domain) {
    const splited = domain.split('.');
    if (!this.rules[splited[splited.length - 1]] || this.rules[splited[splited.length - 1]].indexOf(domain) === -1)
      return false;

    return true;
  }
}

export default Rules;
