import protobufB64 from '@protobufjs/base64';
import root from './proto-root.proto';

class Proto {
  constructor() {
    sdk.getBase64ConnectionString = this.getBase64ConnectionString;
    sdk.parseBase64DeviceInfo = this.parseBase64DeviceInfo;
    sdk.getBase64DeviceInfo = this.getBase64DeviceInfo;
  }

  async getBase64DeviceInfo(deviceInfo) {
    const Message = root.proto.api.DeviceInfo;
    const message = Message.create(deviceInfo);
    const buffer = Message.encode(message).finish();
    let base64 = protobufB64.encode(buffer, 0, buffer.length);
    base64 = base64
      .replace(/\+/gi, '-')
      .replace(/\//gi, '_')
      .replace(/=/gi, '');
    return base64;
  }

  parseBase64DeviceInfo(base64DeviceInfo) {
    const Message = root.proto.api.DeviceInfo;
    const buffer = Uint8Array.from(atob(base64DeviceInfo), c => c.charCodeAt(0));
    // protobufB64.decode(base64DeviceInfo, buffer, protobufB64.length(base64DeviceInfo));
    const message = Message.decode(buffer);
    return Message.toObject(message);
  }

  getBase64ConnectionString(connectionStringObj) {
    const Message = root.AF.ConnectString;
    const message = Message.create(connectionStringObj);
    const buffer = Message.encode(message).finish();
    return protobufB64.encode(buffer, 0, buffer.length);
  }

  request(messageName, urlPrefix, content) {
    return new Promise((resolve, reject) => {
      debug(`Proto request: ${urlPrefix}`);
      debug(JSON.stringify(content));
      debug('-------------------------------------');
      const Message = root.proto.api.request[messageName];
      const err = Message.verify(content);
      if (err) {
        error('request', err, messageName, urlPrefix, content);
      }
      const message = Message.create(content);
      const buffer = Message.encode(message).finish();

      this.sendRequestFetch(messageName, urlPrefix, buffer)
        .then(result => {
          try {
            if (!result.ResponseStatus.success) return reject(result);
            return resolve(result);
          } catch (e) {
            return reject(result);
          }
        })
        .catch(status => {
          const result = {
            ResponseStatus: {
              success: false,
              error_code: status,
              error_message: null
            }
          };

          reject(result);
        });
    });
  }

  async response(messageName, response) {
    try {
      const Message = root.proto.api.response[messageName];
      let content = Message.decode(new Uint8Array(response));
      content = this.transformToObject(content);

      debug(`Proto response: ${messageName}`);
      debug(JSON.stringify(content));
      debug('-------------------------------------');

      return content;
    } catch (e) {
      console.error(`response for ${messageName}`, e, response);
      throw e;
    }
  }

  async sendRequestFetch(messageName, urlPrefix, buffer) {
    try {
      const result = await fetch(sdk.config.elite.api + urlPrefix, { method: 'POST', body: buffer });
      const arrayBuffer = await result.arrayBuffer();
      return this.response(messageName, arrayBuffer);
    } catch (e) {
      console.error('request failed', e, messageName, urlPrefix, buffer);
      throw e;
    }
  }

  transformToObject(message) {
    const object = {};

    for (const k in message) {
      if (typeof message[k] === 'function') {
        continue;
      }

      if (message[k] === null) {
        object[k] = message[k];
      } else if (typeof message[k] === 'object') {
        object[k] = this.transformToObject(message[k]);
      } else {
        object[k] = message[k];
      }
    }

    return object;
  }
}

export default Proto;
