import sdk from '@common-shared/SDK/';
import { binarySearchStringIncludes } from '../Utils/BinarySearch';

const STATUS = 'Connection.Tracker.status';
const COUNTER = 'Connection.Tracker.counter';

const URL = `${sdk.config.protocolPrefix}://${sdk.config.application.applicationId}/static/data/trackers`;

class TrackerBlocker {
  constructor() {
    this.status = sdk.storage.get(STATUS) ? sdk.storage.get(STATUS) : 'off';
    this.counter = sdk.storage.get(COUNTER) ? sdk.storage.get(COUNTER) : 0;

    this.loadRules().then(() => {
      setInterval(() => {
        sdk.storage.set(COUNTER, this.counter);
      }, 30000);
    });
  }

  getCounter() {
    return this.counter;
  }

  getStatus() {
    return this.status;
  }

  setStatus(status) {
    if (['on', 'off', 'disabled'].indexOf(status) === -1) return;
    this.status = status;
    return sdk.storage.set(STATUS, status);
  }

  async loadRules() {
    try {
      const response = await fetch(URL);
      const rules = await response.text();
      this.rules = rules.split(' ');
    } catch (e) {
      error(e);
    }
  }

  check(domain) {
    if (this.status !== 'on' || !domain) return false;
    if (binarySearchStringIncludes(this.rules, domain)) {
      this.counter++;
      return true;
    }
    return false;
  }
}

export default TrackerBlocker;
