import { parseUrlDomain } from '@common-shared/Utils/DomainUtils';
import sdk from '@common-shared/SDK/';
import Signal from './Signal';

const FAVICONS = 'Tabs.favicons';

class Tabs {
  constructor() {
    this.data = {
      tabs: [],
      active: {
        id: undefined,
        domain: undefined,
        title: undefined
      }
    };

    this.favicons = sdk.storage.get(FAVICONS) ? sdk.storage.get(FAVICONS) : {};

    this.createSignals();
  }

  init() {
    return this.updateList().then(() => {
      this.pageView();
      this.listeners();

      sdk.tabs = {
        active: this.data.active,
        openPage: this.openPage.bind(this),
        addFavicon: this.addFavicon.bind(this),
        getFavicon: this.getFavicon.bind(this),
        getByTabId: this.getByTabId.bind(this),
        getActiveTab: this.getActiveTab.bind(this),
        getActiveWebsiteTab: this.getActiveWebsiteTab.bind(this),
        getByWindowId: this.getByWindowId.bind(this),
        onPageView: this.onPageView,
        onTabChanged: this.onTabChanged,
        onDomainChanged: this.onDomainChanged
      };
      console.log(sdk);
    });
  }

  listeners() {
    browser.tabs.onRemoved.addListener(this.updateList.bind(this));
    browser.tabs.onDetached.addListener(this.updateList.bind(this));
    browser.tabs.onReplaced.addListener(this.updateList.bind(this));
    browser.tabs.onActivated.addListener(this.updateList.bind(this));
  }

  updateList() {
    return new Promise(resolve => {
      browser.tabs.query({}, tabs => {
        this.data.tabs = [];
        for (const k in tabs) {
          if (!tabs[k] || !tabs[k].url || !tabs[k].url.match(/^https?/)) continue;

          const record = this.createRecord(tabs[k]);
          if (record) this.data.tabs.push(record);
        }

        this.getActiveTab().then(activeTab => {
          const old = {
            id: this.data.active.id,
            domain: this.data.active.domain,
            title: this.data.active.title
          };

          this.data.active.id = activeTab.id;
          this.data.active.domain = activeTab.domain;
          this.data.active.title = activeTab.title;

          if (old.id && old.id !== activeTab.id) {
            this.onTabChanged.dispatch(activeTab);
          } else if (old.id && old.domain !== activeTab.domain) {
            this.onDomainChanged.dispatch(activeTab);
          }

          return resolve();
        });
      });
    });
  }

  addFavicon(domain, url) {
    if (!this.favicons[domain]) this.favicons[domain] = url;
    sdk.storage.set(FAVICONS, this.favicons);
  }

  getFavicon(domain) {
    return this.favicons[domain] ? this.favicons[domain] : undefined;
  }

  getActiveTab() {
    return new Promise(resolve => {
      browser.tabs.query({ active: true, currentWindow: true }, tabs => {
        if (tabs && tabs[0]) return resolve(this.createRecord(tabs[0]));

        browser.tabs.query({ active: true }, tabs => {
          if (tabs && tabs[0]) return resolve(this.createRecord(tabs[0]));

          return resolve({ id: undefined, domain: undefined, title: undefined });
        });
      });
    });
  }

  async getActiveWebsiteTab() {
    const tab = await this.getActiveTab();
    if (/^https?:\/\/.*?/.test(tab.url)) {
      return tab;
    }
    return undefined;
  }

  getByTabId(id) {
    if (!id || typeof id !== 'number') return false;

    for (const k in this.data.tabs) {
      const tab = this.data.tabs[k];
      if (tab.id === id) return tab;
    }

    return false;
  }

  getByWindowId(id) {
    if (!id || typeof id !== 'number') return false;

    for (const k in this.data.tabs) {
      const tab = this.data.tabs[k];
      if (tab.windowId === id) return tab;
    }

    return false;
  }

  createRecord(tab) {
    if (!tab) return undefined;

    const record = {
      id: tab.id,
      url: tab.url,
      title: tab.title,
      domain: parseUrlDomain(tab.url),
      windowId: tab.windowId
    };

    if (tab.favIconUrl) this.addFavicon(record.domain, tab.favIconUrl);

    return record;
  }

  createSignals() {
    this.onPageView = new Signal();
    this.onTabChanged = new Signal();
    this.onDomainChanged = new Signal();
  }

  getDomain(url) {
    if (!url) return undefined;

    const parsed = url.toLowerCase().match(/^[^:]+:\/\/(www\.)?([^?/\\]+)/);
    if (!parsed || !parsed[2]) return undefined;

    return parsed[2];
  }

  pageView() {
    browser.webRequest.onResponseStarted.addListener(
      details => {
        if (!details || details.tabId === -1 || details.type !== 'main_frame') return;

        const domain = parseUrlDomain(details.url);
        if (!domain) return;

        this.updateList().then(() => {
          this.onPageView.dispatch({ domain });
        });
      },
      { urls: ['http://*/*', 'https://*/*'] },
      ['responseHeaders']
    );
  }

  openPage(url, parameters) {
    return new Promise(resolve => {
      parameters = parameters || {};

      if (parameters.mode === 'popup') {
        const width = parameters.width || 580;
        const height = parameters.height || 600;
        const left = screen.width / 2 - width / 2;
        const top = screen.height / 2 - height / 2;

        browser.windows.create(
          {
            url,
            width: Math.round(width),
            height: Math.round(height),
            left: Math.round(left),
            top: Math.round(top),
            focused: true,
            type: 'popup'
          },
          window => {
            resolve(window.tabs[0]);
          }
        );
        return;
      }

      browser.tabs.create({ url }, tab => {
        resolve(tab);
      });
    });
  }
}

export default Tabs;
