class DomRules {
  static step1(rules) {
    const temp = { none: [] };

    for (const k in rules) {
      const rule = rules[k];
      let [r, domains, filter] = rule.match(/([^#]*)##(.*)/);

      domains = domains.match(/[^,]+/g);

      if (!domains) {
        temp.none.push(filter);
        continue;
      }

      for (const k in domains) {
        const domain = domains[k];
        if (!temp[domain]) temp[domain] = [];

        temp[domain].push(filter);
      }
    }

    for (const k in temp) {
      temp[k] = temp[k].join(', ');
    }

    return temp;
  }
}

export default DomRules;
