import sdk from '@common-shared/SDK';
import sampleSize from 'lodash/sampleSize';
import { uuidv4 } from '@common-shared/Utils/UuidGenerator';

const captivePortalLinks = [
  { link: 'http://google.com/generate_204', domain: 'google.com' },
  { link: 'http://gstatic.com/generate_204', domain: 'gstatic.com' },
  { link: 'http://maps.google.com/generate_204', domain: 'maps.google.com' },
  { link: 'http://www.google.com/generate_204', domain: 'www.google.com' },
  { link: 'http://clients3.google.com/generate_204', domain: 'clients3.google.com' }
];

const httpsCertificateLinks = [
  { link: 'https://google.com/', domain: 'google.com' },
  { link: 'https://apple.com', domain: 'apple.com' },
  { link: 'https://microsoft.com', domain: 'microsoft.com' },
  { link: 'https://yahoo.com', domain: 'yahoo.com' },
  { link: 'https://baidu.com', domain: 'baidu.com' },
  { link: 'https://amazon.com', domain: 'amazon.com' },
  { link: 'https://instagram.com', domain: 'instagram.com' },
  { link: 'https://linkedin.com', domain: 'linkedin.com' },
  { link: 'https://ebay.com', domain: 'ebay.com' },
  { link: 'https://bing.com', domain: 'bing.com' },
  { link: 'https://samsung.com', domain: 'samsung.com' },
  { link: 'https://goo.gl', domain: 'goo.gl' },
  { link: 'https://wikipedia.org', domain: 'wikipedia.org' },
  { link: 'https://live.com', domain: 'live.com' },
  { link: 'https://office.com', domain: 'office.com' }
];
const errorMap = ['success', 'internal error', 'connection error', 'connection stuck', 'no network', 'failed'];
export default class ConnectionReport {
  catime = null;

  beforeConnectionIp = null;

  idAddressChanged = null;

  seqNo = 0;

  getCurrentIpAddress = async () => {
    try {
      const response = await fetch('https://www.hsselite.com/ipinfo');
      const { ip } = await response.json();
      return ip;
    } catch {
      return null;
    }
  };

  onBeforeConnectionStartPreparation = async () => {
    this.idAddressChanged = null;
    this.catime = null;
    this.beforeConnectionIp = await this.getCurrentIpAddress();
  };

  onAfterConnectionStartReport = async ({ location = sdk.proxy.status.location, reconnect = false } = {}) => {
    this.catime = Date.now();
    await this.reportConnectionStart();
    await this.reportConnectionStartDetails({ location, reconnect });
  };

  onAfterConnectionEndReport = async ({ error = false, cancel } = {}) => {
    await this.reportConnectionEnd();
    if (!cancel && (error || this.idAddressChanged === false)) {
      await this.reportConnectionEndDetailed();
    }
  };

  reportConnectionStart = async () => {
    sdk.reporting.internal('connection_start', {
      duration: Date.now() - this.catime
    });
  };

  reportConnectionStartDetails = async ({ location, reconnect }) => {
    let servers = sdk.proxy.getActiveServers();
    if (!servers?.length) {
      servers = sdk.servers.getServers();
    }
    const server = servers[0];
    const detailedPayload = {};
    const afterConnectionIp = await this.getCurrentIpAddress();
    let errorCode = 0;

    // TODO: handle ip change flag aka success proxy ip usage on top level
    this.idAddressChanged = afterConnectionIp !== this.beforeConnectionIp;
    if (!this.idAddressChanged) {
      errorCode = 1;
      const networkAvailabilityDetails = await this.getNetworkAvailabilityDetails({ location, reconnect, server });
      detailedPayload.network_availability = (
        networkAvailabilityDetails.filter(({ result }) => result === 'ok').length / networkAvailabilityDetails.length
      ).toFixed(2);
      detailedPayload.notes = {
        network_availability_test: networkAvailabilityDetails
      };
    }

    const deviceInfo = sdk.getDeviceInfo();

    detailedPayload.details = [
      {
        server_domain: server.address,
        server_port: server.port,
        duration: Date.now() - this.catime,
        error_code: errorCode,
        catime: this.catime,
        sd_config_source: sdk.config.hss ? 'hss' : 'embedded',
        protocol: 'https',
        to_country: location,
        reason: reconnect ? 'a_reconnect' : 'm_ui',
        device_hash: deviceInfo.hash,
        caid: uuidv4()
          .replaceAll('-', '')
          .toUpperCase(),
        app_name: deviceInfo.package_name,
        app_version: sdk.config.application.version,
        user_type: sdk.user.status.elite ? 'premium' : 'free',
        error: errorMap[errorCode],
        ucr_sd_source: '',
        seq_no: this.seqNo++
      }
    ];
    sdk.reporting.internal('connection_start_detailed', detailedPayload);
  };

  reportConnectionEnd = async () => {
    sdk.reporting.internal('connection_end', {
      duration: Date.now() - this.catime
    });
  };

  reportConnectionEndDetailed = async () => {
    const detailedPayload = {};
    const networkAvailabilityDetails = await this.getNetworkAvailabilityDetails();
    detailedPayload.network_availability = (
      networkAvailabilityDetails.filter(({ result }) => result === 'ok').length / networkAvailabilityDetails.length
    ).toFixed(2);
    detailedPayload.notes = {
      network_availability_test: networkAvailabilityDetails
    };
    sdk.reporting.internal('connection_end_detailed', detailedPayload);
  };

  getNetworkAvailabilityDetails = async () => {
    const detailedResults = [];
    try {
      for (const { link } of captivePortalLinks) {
        // eslint-disable-next-line no-await-in-loop
        const { ok, status } = await fetch(link);
        let errorCode = 0;
        if (!ok) {
          errorCode = 2;
        }

        let result = 'ok';
        if (status !== 204 && ok) {
          result = 'wall';
        } else if (!ok) {
          result = 'invalid';
        }

        detailedResults.push({
          type: 'captive portal',
          url: link,
          result
        });
      }

      for (const { link } of sampleSize(httpsCertificateLinks, 8)) {
        // eslint-disable-next-line no-await-in-loop
        const { ok } = await fetch(link);
        let errorCode = 0;
        if (!ok) {
          errorCode = 2;
        }
        detailedResults.push({
          type: 'http certificate',
          url: link,
          result: ok ? 'ok' : 'invalid'
        });
      }
    } catch (e) {
      // ignore
    }
    return detailedResults;
  };
}
