class Speed {
  constructor() {
    this.startRequestHashMap = {};
    this.speed = null;
  }

  get() {
    return this.speed;
  }

  reset() {
    this.startRequestHashMap = {};
  }

  update({ start, end, headers, remove }) {
    if (start) {
      this.startRequestHashMap[start.requestId] = start.timeStamp;
    }
    if (headers && end) {
      const content = headers.find(h => h.name === 'content-length');
      if (!content) return;
      const contentValue = Number((Number(content.value) / 1048576).toFixed(4));
      const startReqTimestamp = this.startRequestHashMap[end.requestId];
      const diff = (end.timeStamp - startReqTimestamp) / 1000;
      const speed = contentValue / diff;
      this.speed = speed;
    }

    if (remove) {
      delete this.startRequestHashMap[remove];
    }
  }
}

export default Speed;

