import Sword from '@common-shared/Sword';
import WebRTC from '@common-shared/WebRTC';
import AdBlocker from '@common-shared/AdBlocker';
import DnsPrefetch from '@common-shared/DnsPrefetch';
import SitesCounter from '@common-shared/SitesCounter';
import MalwareBlocker from '@common-shared/MalwareBlocker';
import TrackerBlocker from '@common-shared/TrackerBlocker';
import CookiesBlocker from '@common-shared/CookiesBlocker';
import BandwidthCounter from '@common-shared/BandwidthCounter';
import LimitUsage from '@common-shared/LimitUsage';
import { parseUrlDomain } from '@common-shared/Utils/DomainUtils';
import Speed from '@common-shared/Proxy/Speed';

class RequestMonitoring {
  constructor({ sword = false } = {}) {
    this.webRTC = new WebRTC();
    this.adBlocker = new AdBlocker();
    this.dnsPrefetch = new DnsPrefetch();
    this.sitesCounter = new SitesCounter();
    this.malwareBlocker = new MalwareBlocker();
    this.trackerBlocker = new TrackerBlocker();
    this.cookiesBlocker = new CookiesBlocker();
    this.bandwidthCounter = new BandwidthCounter();
    this.limitUsage = new LimitUsage();

    sdk.sitesCounter = this.sitesCounter.getCounter.bind(this.sitesCounter);
    sdk.bandwidthCounter = this.bandwidthCounter.getCounter.bind(this.bandwidthCounter);
    sdk.limitUsage = this.limitUsage.getLimit.bind(this.limitUsage);

    sdk.cookiesBlocker = {
      getStatus: this.cookiesBlocker.getStatus.bind(this.cookiesBlocker),
      setStatus: this.cookiesBlocker.setStatus.bind(this.cookiesBlocker),
      getCounter: this.cookiesBlocker.getCounter.bind(this.cookiesBlocker)
    };
    sdk.adBlocker = {
      setStatus: this.adBlocker.setStatus.bind(this.adBlocker),
      getStatus: this.adBlocker.getStatus.bind(this.adBlocker),
      getCounter: this.adBlocker.getCounter.bind(this.adBlocker),
      getDomRules: this.adBlocker.getDomRules.bind(this.adBlocker)
    };
    sdk.trackerBlocker = {
      setStatus: this.trackerBlocker.setStatus.bind(this.trackerBlocker),
      getStatus: this.trackerBlocker.getStatus.bind(this.trackerBlocker),
      getCounter: this.trackerBlocker.getCounter.bind(this.trackerBlocker)
    };
    sdk.malwareBlocker = {
      setStatus: this.malwareBlocker.setStatus.bind(this.malwareBlocker),
      getStatus: this.malwareBlocker.getStatus.bind(this.malwareBlocker),
      getCounter: this.malwareBlocker.getCounter.bind(this.malwareBlocker),
      proceed: this.malwareBlocker.proceed.bind(this.malwareBlocker)
    };
    sdk.webRTC = this.webRTC;

    if (sword) {
      this.sword = new Sword();
      sdk.sword = {
        message: this.sword.message.bind(this.sword),
        getList: this.sword.getList.bind(this.sword),
        setStatus: this.sword.setStatus.bind(this.sword),
        getStatus: this.sword.getStatus.bind(this.sword),
        addToList: this.sword.addToList.bind(this.sword),
        removeFromList: this.sword.removeFromList.bind(this.sword)
      };
    }

    const speed = new Speed();
    sdk.speed = {
      get: speed.get.bind(speed),
      update: speed.update.bind(speed),
      reset: speed.reset.bind(speed)
    };

    this.onBeforeRequest();
    this.onHeadersRecived();
    this.onBeforeSendHeaders();
    this.onCompletedHeaders();
  }

  onBeforeRequest() {
    browser.webRequest.onBeforeRequest.addListener(
      details => {
        if (details.tabId === -1 || !this.isConnected(details.tabId)) return;
        let start = Date.now();
        sdk.speed.update({
          start: {
            requestId: details.requestId,
            timeStamp: details.timeStamp
          }
        });

        const check = this.sword?.onBeforeRequest(details);
        if (check) {
          debug(`Sword redirect: ${Date.now() - start} ${check}`);
          return { redirectUrl: check };
        }

        start = Date.now();
        const domain = parseUrlDomain(details.url);
        if (this.malwareBlocker?.check(domain)) {
          if (details.type === 'main_frame') {
            debug(`malwareBlocker redirect: ${Date.now() - start} ${details.url}`);
            sdk.reporting.report('malware', 'blocked', domain);
            return {
              redirectUrl: `${sdk.config.protocolPrefix}://${
                sdk.config.application.applicationId
              }/pages/blocked.html?url=${escape(details.url)}`
            };
          }
          sdk.reporting.report('malware', 'canceled', domain);
          debug(`malwareBlocker cancel: ${Date.now() - start} ${details.url}`);
          return { cancel: true };
        }

        start = Date.now();
        if (this.trackerBlocker?.check(domain)) {
          debug(`trackerBlocker: ${Date.now() - start} ${details.url}`);
          return { cancel: true };
        }
      },
      { urls: ['http://*/*', 'https://*/*'] },
      ['blocking', 'requestBody']
    );
  }

  onBeforeSendHeaders() {
    browser.webRequest.onBeforeSendHeaders.addListener(
      details => {
        if (details.tabId === -1 || !this.isConnected(details.tabId)) return;

        let start = Date.now();
        if (this.adBlocker?.check(details)) {
          debug(`AdBlocker: ${Date.now() - start} ${details.url}`);
          return { cancel: true };
        }

        start = Date.now();
        const header = this.sword?.onBeforeSendHeaders?.(details);
        if (header) {
          debug(`Sword modify headers: ${Date.now() - start} ${details.url}`);
        }

        return { requestHeaders: this.cookiesBlocker?.check(details.requestHeaders) };
      },
      { urls: ['http://*/*', 'https://*/*'] },
      ['blocking', 'requestHeaders']
    );
  }

  onHeadersRecived() {
    browser.webRequest.onHeadersReceived.addListener(
      details => {
        if (details.tabId === -1 || !this.isConnected(details.tabId)) return;

        // this.bandwidthCounter.count(details.responseHeaders);

        const check = this.sword?.onHeadersReceived(details);
        if (check) {
          return { responseHeaders: check };
        }
      },
      { urls: ['<all_urls>'] },
      ['blocking', 'responseHeaders']
    );
  }

  onCompletedHeaders() {
    browser.webRequest.onCompleted.addListener(
      details => {
        if (details.tabId === -1 || !this.isConnected(details.tabId)) return;

        this.bandwidthCounter.count(details.responseHeaders);
        sdk.speed.update({
          end: {
            requestId: details.requestId,
            timeStamp: details.timeStamp
          },
          headers: details.responseHeaders
        });
        sdk.speed.update({ remove: details.requestId });
      },
      { urls: ['<all_urls>'] },
      ['responseHeaders']
    );
  }

  isConnected(id) {
    if (!id) return false;

    const tab = sdk.tabs.getByTabId(id);
    if (!tab) return false;

    if (sdk.proxy.bypass.isInList(tab.domain)) return false;
    if (sdk.proxy.secured.isInList(tab.domain)) return true;
    if (sdk.proxy.status.status === 'connected') return true;

    return false;
  }
}

export default RequestMonitoring;
