import sdk from '@common-shared/SDK';

class Timer {
  constructor(proxy) {
    this.startTime = 0;
    this.status = sdk.proxy.status.status;

    if (this.status === 'connected') {
      this.startTime = Date.now();
    }

    sdk.proxy.onStatusChanged.add(status => {
      if (['connected', 'disconnected'].indexOf(status.status) === -1) return;

      if (this.status === 'connected' && status.status !== 'connected') {
        this.startTime = 0;
      }

      if (status.status === 'connected' && this.status !== 'connected') {
        this.startTime = Date.now();
      }

      this.status = status.status;
    });
  }

  get() {
    return this.startTime;
  }
}

export default Timer;
