import memoize from 'lodash/memoize';
import sdk from '@common-shared/SDK';

class Authorization {
  constructor() {
    this.lastProxyHost = '';
    this.tryCounter = { new: 0, default: 0, hss: 0 };

    const getServerByAddress = memoize(sdk.proxy.getServerByAddress);

    browser.webRequest.onAuthRequired.addListener(
      details => {
        if (!details.isProxy) {
          return;
        }
        const server = getServerByAddress(details.challenger.host);
        if (!server) {
          return;
        }
        if (this.tryCounter.new >= 1 && this.tryCounter.old >= 1) {
          sdk.servers.disableServer(details.challenger.host);
          return { cancel: true };
        }
        const authCredentials = this.getAuthCredentials(details.challenger.host);
        return { authCredentials };
      },
      { urls: ['http://*/*', 'https://*/*'] },
      ['blocking']
    );
  }

  getAuthCredentials(host) {
    if (this.lastProxyHost !== host) {
      this.lastProxyHost = host;
      this.tryCounter = { new: 0, default: 0, hss: 0 };
    }

    if (sdk.config.infra === 'hss') {
      debug('try hss auth');
      this.tryCounter.hss += 1;
      const connectingString = sdk.connectionString({ serverDomain: host, requestedCountry: 'US', serverPort: 433 });
      const base64ConnectionString = sdk.getBase64ConnectionString(connectingString);
      debug('connectingString', JSON.stringify(connectingString));
      debug('base64ConnectionString', base64ConnectionString);
      debug('base64ConnectionString', base64ConnectionString);
      const payload = {
        username: sdk.user.status.hss.username,
        // username: '1',
        password: `${base64ConnectionString}$${sdk.user.status.hss.password}`
        // password: `NDU2$aAYQU87XBWcQdjfpLOR5wx5mFC5PVDVtLYQtLeqLejUqI82sU/DNu/b9VOElYjichJJeXOvTQ0SST3hIz6w0TWd1tdR8z4mhIf2AFZFyOKcAPag4HDnzX2LWore2`
      };
      debug('payload', JSON.stringify(payload));
      return payload;
    }
    if (this.tryCounter.new <= this.tryCounter.default) {
      this.tryCounter.new += 1;
      return {
        username: sdk.user.credentials.prod.username,
        password: sdk.user.credentials.prod.password
      };
    }
    this.tryCounter.default += 1;
    return {
      username: sdk.user.credentials.old.username,
      password: sdk.user.credentials.old.password
    };
  }
}

export default Authorization;
