import sdk from '@common-shared/SDK/';
import DomRules from './DomRules';
import RequestRules from './RequestRules';

const URL = `${sdk.config.protocolPrefix}://${sdk.config.application.applicationId}/static/data/adblock`;
const SAVED_RULES = 'AdBlocker.Rules.savedRules';

class Rules {
  getRules() {
    return new Promise(resolve => {
      const rules = sdk.storage.get(SAVED_RULES);
      if (rules) {
        rules[0] = RequestRules.step2(rules[0]);
        return resolve(rules);
      }

      this.loadRules().then(rules => {
        let [requestRules, domRules] = this.splitRulesByType(rules);
        domRules = DomRules.step1(domRules);
        requestRules = RequestRules.step1(requestRules);

        sdk.storage.set(SAVED_RULES, [requestRules, domRules]).then(() => {
          requestRules = RequestRules.step2(requestRules);

          return resolve([requestRules, domRules]);
        });
      });
    });
  }

  splitRulesByType(rules) {
    rules = rules.replace(/[ ]/g, '').match(/[^\r\n]+/g);
    const domRules = [];
    const requestRules = [];

    for (const k in rules) {
      const rule = rules[k];
      if (rule === '' || /^\/.*\/(\$|$)/.test(rule) || /^!|\[|#@#/.test(rule)) continue;

      if (/##/.test(rule)) {
        domRules.push(rule);
      } else {
        requestRules.push(rule);
      }
    }

    return [requestRules, domRules];
  }

  async loadRules() {
    try {
      const response = await fetch(URL);
      return response.text();
    } catch (e) {
      return '';
    }
  }
}

export default Rules;
