import sdk from '@common-shared/SDK';

const NEXT_PROMO = 'DesktopMessages.nextPromoMessage';
const WHAT_CONNECT = 'DesktopMessages.whatConnect';

class DesktopMessages {
  constructor() {
    sdk.proxy.onProxyError.add(this.proxyError.bind(this));
    sdk.proxy.onStatusChanged.add(this.proxyStatusChanged.bind(this));

    browser.notifications.onClosed.addListener(id => {
      const type = this.checkType(id);
      if (!type) return;

      sdk.reporting.report('message', type, 'closed');
    });

    browser.notifications.onButtonClicked.addListener((id, buttonId) => {
      const type = this.checkType(id);
      if (!type) return;

      if (buttonId === 1) {
        browser.notifications.clear(id, () => {
          sdk.reporting.report('message', type, 'buttonClose');
        });
      } else if (buttonId === 0 && type === 'error') {
        browser.notifications.clear(id, () => {
          sdk.reporting.report('message', type, 'buttonDownload');
          if (navigator.platform.indexOf('Mac') !== -1)
            window.open(
              'https://control.kochava.com/v1/cpi/click?campaign_id=kohotspot-shield-e1z5meb8bd0bc517b5d1&network_id=7450&site_id=1&device_id=device_id'
            );
          else
            window.open(
              'https://control.kochava.com/v1/cpi/click?campaign_id=kohotspot-shield-2oo5a539acac1e6b0f&network_id=7450&site_id=1&device_id=device_id'
            );
        });
      } else if (buttonId === 0 && type === 'promo') {
        browser.notifications.clear(id, () => {
          sdk.reporting.report('message', type, 'buttonDownload');
          if (navigator.platform.indexOf('Mac') !== -1)
            window.open(
              'https://control.kochava.com/v1/cpi/click?campaign_id=kohotspot-shield-e1z5meb8bd0bc517b5d1&network_id=7450&site_id=1&device_id=device_id'
            );
          else
            window.open(
              'https://control.kochava.com/v1/cpi/click?campaign_id=kohotspot-shield-2oo5a539acac1e6b0f&network_id=7450&site_id=1&device_id=device_id'
            );
        });
      } else if (buttonId === 0 && type === 'update') {
        browser.notifications.clear(id, () => {});
      }
    });
  }

  proxyStatusChanged(event) {
    if (event?.status !== 'connected') return;

    let connect = sdk.storage.get(WHAT_CONNECT) ? sdk.storage.get(WHAT_CONNECT) : 0;
    connect++;
    sdk.storage.set(WHAT_CONNECT, connect);
    if (connect < 2) {
      return;
    }

    setTimeout(() => {
      this.showProxyStatusMessage();
    }, 15000);
  }

  showProxyStatusMessage() {
    const nextPromoMessage = sdk.storage.get(NEXT_PROMO) ? sdk.storage.get(NEXT_PROMO) : 0;

    if (nextPromoMessage >= Date.now()) return;

    browser.notifications.create(
      `promoMessage${Date.now()}`,
      {
        type: 'image',
        title: 'Try our Desktop VPN Client',
        iconUrl: '/static/assets/messages/logo.svg',
        imageUrl: '/static/assets/messages/block.png',
        message: 'Protect your entire internet connection, not just browser',
        requireInteraction: true,
        buttons: [{ title: 'Start Free Download' }, { title: 'Next time' }]
      },
      () => {
        sdk.reporting.report('message', 'promo', 'showed');
        sdk.storage.set(NEXT_PROMO, Date.now() + 7 * 24 * 60 * 60 * 1000);
      }
    );
  }

  proxyError(action) {
    if (action !== 'disconnect') return;

    browser.notifications.create(
      `errorMessage${Date.now()}`,
      {
        type: 'basic',
        title: 'Connection alert',
        iconUrl: '/static/assets/messages/warning.png',
        message: 'We are having troubles. Download Hotspot Shield to get secure and unrestricted access',
        requireInteraction: true,
        buttons: [{ title: 'Fix Connection' }, { title: 'Close' }]
      },
      () => {
        sdk.reporting.report('message', 'error', 'showed');
      }
    );
  }

  checkType(id) {
    const name = id.match(/^([a-z]+)Message/);
    if (!name || !name[1]) return undefined;

    switch (name[1]) {
      case 'error':
        return 'error';
      case 'promo':
        return 'promo';
      case 'update':
        return 'update';
      default:
        return undefined;
    }
  }
}

export default DesktopMessages;
