import { isChrome, isEdge, isFirefox } from '@common-shared/BrowserDetection';

const getDecimalVersionPart = part => {
  if (+part >= 10) {
    return part;
  }
  return `0${part}`;
};

export const getDecimalVersion = version => {
  const [major, minor, rev] = version.split('.').map(v => +v);
  return +`${major}${getDecimalVersionPart(minor)}${getDecimalVersionPart(rev)}`;
};

export const getVersionFormats = () => {
  const version = browser.runtime.getManifest().version;
  const decimalVersion = getDecimalVersion(version);
  let connectStringPlatform;
  if (isFirefox) {
    connectStringPlatform = 9; // BROWSER_FIREFOX = 9;
  } else if (isChrome) {
    connectStringPlatform = 8; // BROWSER_CHROME = 8;
  } else if (isEdge) {
    connectStringPlatform = 10; // BROWSER_EDGE = 10;
  }

  return { version, decimalVersion, connectStringPlatform };
};
