import Proto from './proto/Proto';

const DATA = 'User.data';

class Elite {
  constructor() {
    this.popUp = {
      deviceInfo: '',
      trial: 'https://www.hsselite.com/payment/cc/week2month?chrome_ext=1',
      payment: 'https://www.hsselite.com/pre_purchase?chrome_ext=1'
    };

    this.proto = new Proto();
    sdk.getDeviceInfo = this.deviceInfo.bind(this);
    sdk.getSD = this.getSD;
    sdk.getFreeSD = this.getFreeSD;
    sdk.getEliteSD = this.getEliteSD;
    sdk.connectionString = this.connectionString.bind(this);

    this.updateStatusInterval = 1000 * 60 * 60 * 12; // 12h
  }

  init() {
    return new Promise(resolve => {
      this.getStatus().then(() => {
        this.getPopUp();
        this.showToPublic();
        setInterval(() => {
          this.getStatus();
        }, this.updateStatusInterval);
        return resolve();
      });
    });
  }

  async getStatus() {
    let result;
    try {
      result = await this.proto.request('Status', 'status', { DeviceInfo: this.deviceInfo() });
    } catch (error) {
      result = error;
    }
    await this.statusCallback(result);
  }

  getFreeSD = async () => this.getSD('free');

  getEliteSD = async () => this.getSD('elite');

  getSD = async type => {
    try {
      const result = await this.proto.request('SD', 'config/sd', {
        DeviceInfo: { ...this.deviceInfo(), package_name: 'com.anchorfree.extchrome' }, // handle SD issue with package_name
        requested_country: 'OP',
        additional_data: `packages%5B%5D=${type}`
      });
      let serves;
      if (result && result.raw_data) {
        try {
          const tmp = JSON.parse(result.raw_data);
          if (tmp.servers) {
            serves = tmp.servers;
          }
        } catch (e) {}
      }
      return serves;
    } catch (e) {
      return undefined;
    }
  };

  async signIn(login, password) {
    try {
      this.eliteEvent({ category: 'signin' });
      const result = await this.proto.request('Signin', 'signin', { DeviceInfo: this.deviceInfo(), login, password });
      const status = await this.statusCallback(result);
      this.eliteEvent({ category: 'signedin', touchpoint: sdk.user.status.elite ? 'elite' : 'free' });
      if (sdk.user.status.elite) {
        sdk.forceLoadSD();
      }
      return status;
    } catch (e) {
      throw e?.ResponseStatus || e;
    }
  }

  async signUp(login, password) {
    try {
      this.eliteEvent({ category: 'signup' });
      const result = await this.proto.request('Signup', 'signup', {
        DeviceInfo: this.deviceInfo(),
        email: login,
        password
      });
      return await this.signIn(login, password);
    } catch (e) {
      throw e?.ResponseStatus || e;
    }
  }

  async restorePassword(login) {
    try {
      this.eliteEvent({ category: 'restorepassword' });
      return await this.proto.request('RestorePassword', 'restore', { DeviceInfo: this.deviceInfo(), email: login });
    } catch (e) {
      throw e?.ResponseStatus || e;
    }
  }

  getPopUp() {
    this.proto.request('Config', 'config/payment_popup', { DeviceInfo: this.deviceInfo() }).then(result => {
      if (!result?.ResponseStatus?.success) return;

      if (result.PaymentPopupGeneral?.url) this.popUp.payment = result.PaymentPopupGeneral.url;
      if (result.PaymentPopupOptin?.url) this.popUp.trial = result.PaymentPopupOptin.url;

      this.proto.getBase64DeviceInfo(this.deviceInfo()).then(deviceInfo => {
        this.popUp.deviceInfo = deviceInfo;
      });
    });
  }

  openPopUp(touchpoint, domain) {
    this.domain = domain;
    this.touchpoint = touchpoint;

    const data = {
      category: 'openpopup',
      touchpoint: this.touchpoint
    };

    this.eliteEvent(data);

    const params = `touchpoint=${touchpoint}&domain=${this.domain}&user_co=${sdk.user.country}${
      sdk.proxy.status.status === 'connected' ? `&proxy_co=${sdk.proxy.status.location}` : ''
    }`;

    const url = `${this.popUp.payment + (this.popUp.payment.indexOf('?') !== -1 ? '&' : '?')}pbdi=${
      this.popUp.deviceInfo
    }&ct=${encodeURIComponent(params)}&utm_source=${sdk.config.elite.popupUtm}&utm_medium=Free&utm_campaign=${
      this.touchpoint
    }`;

    sdk.tabs.openPage(url);
  }

  openTrialPromo(touchpoint, domain) {
    this.domain = domain;
    this.touchpoint = touchpoint;

    const data = {
      category: 'opentrialpromo',
      touchpoint: this.touchpoint
    };

    this.eliteEvent(data);

    const params = `touchpoint=${touchpoint}&domain=${this.domain}&user_co=${sdk.user.country}${
      sdk.proxy.status.status === 'connected' ? `&proxy_co=${sdk.proxy.status.location}` : ''
    }`;

    const url = `${this.popUp.trial}&pbdi=${this.popUp.deviceInfo}&ct=${encodeURIComponent(params)}&utm_source=${
      sdk.config.elite.popupUtm
    }&utm_medium=Free&utm_campaign=${this.touchpoint}`;

    sdk.tabs.openPage(url);
  }

  async statusCallback(status) {
    sdk.user.status.token = undefined;
    sdk.user.status.elite = false;
    sdk.user.status.login = undefined;
    sdk.user.status.bnLink = false;
    sdk.user.status.authorized = false;

    if (sdk.config.infra === 'hss' && status.proxy_params) {
      sdk.user.status.hss = {
        password: status.proxy_params.password,
        username: status.proxy_params.username
      };
    }
    if (status.token) {
      sdk.user.status.token = status.token;
    }

    if (
      status.UserStatus &&
      typeof status.UserStatus.is_anonymous !== 'undefined' &&
      !status.UserStatus.is_anonymous &&
      status.UserStatus.login &&
      status.UserStatus.login !== '_AFFREE_'
    ) {
      sdk.user.status.login = status.UserStatus.login;
      sdk.user.status.authorized = true;
      sdk.user.status.bnLink = false;
    }

    if (status.UserStatus && status.UserStatus.packages) {
      for (const k in status.UserStatus.packages) {
        const p = status.UserStatus.packages[k];
        if (p.is_active) {
          sdk.user.status.elite = true;
          break;
        }
      }
    }
    await sdk.storage.set(DATA, sdk.user);
    return JSON.parse(JSON.stringify(sdk.user.status));
  }

  showToPublic() {
    sdk.user.bnLink = this.bnLink.bind(this);
    sdk.user.signUp = this.signUp.bind(this);
    sdk.user.signIn = this.signIn.bind(this);
    sdk.user.signOut = this.signOut.bind(this);
    sdk.user.openPopUp = this.openPopUp.bind(this);
    sdk.user.getStatus = this.getStatus;
    sdk.user.openTrialPromo = this.openTrialPromo.bind(this);
    sdk.user.restorePassword = this.restorePassword.bind(this);
  }

  bnLink(email) {
    const data = {
      category: 'bnLink'
    };

    this.eliteEvent(data);
    sdk.user.status.login = email;
    sdk.user.status.bnLink = true;
    // sdk.storage.set(STATUS, sdk.user.status);

    return this.proto.request('BNLink', 'bn/link', { DeviceInfo: this.deviceInfo(), email }).then(result => {
      if (result && result.link_status && result.link_status === 202 && !this.bnLinkInterval) {
        this.monitoreBnLink();
      }

      return result;
    });
  }

  monitoreBnLink() {
    this.bnLinkInterval = setInterval(() => {
      this.getStatus().then(() => {
        if (sdk.user.status.authorized) {
          clearInterval(this.bnLinkInterval);
          this.bnLinkInterval = null;
          return true;
        }
      });
    }, 10000);
  }

  signOut() {
    const data = {
      category: 'signout'
    };

    this.eliteEvent(data);

    return this.proto.request('Signout', 'signout', { DeviceInfo: this.deviceInfo() }).then(result => {
      return this.statusCallback(result);
    });
  }

  deviceInfo() {
    const deviceInfo = {
      platform: sdk.config.elite.deviceInfo.platform,
      hash: sdk.user.hash,
      package_name: sdk.config.elite.deviceInfo.package_name,
      app_version: sdk.config.application.decimalVersion,
      model: sdk.config.elite.deviceInfo.model,
      make: sdk.config.elite.deviceInfo.make,
      os_name: navigator.userAgent.match(/\(([^);]+)/i) ? navigator.userAgent.match(/\(([^);]+)/i)[1] : 'undefined',
      language: sdk.user.language
    };

    if (sdk.user.status.token) {
      deviceInfo.token = sdk.user.status.token;
    }

    return deviceInfo;
  }

  connectionString({ serverDomain, requestedCountry, serverPort }) {
    return {
      deviceHash: sdk.user.hash,
      serverIP: 0,
      serverPort,
      platform: sdk.config.application.connectStringPlatform,
      protocol: 6, // WEB_PROXY (6)
      clientVersion: sdk.config.application.decimalVersion,
      requestedCountry,
      serverDomain,
      platformInfo: sdk.config.elite.deviceInfo.platform,
      appName: sdk.config.application.appName,
      userLang: sdk.user.language
    };
  }

  eliteEvent(data) {
    return new Promise(resolve => {
      try {
        const status = sdk.proxy.status;

        const event = {
          event: 'elite',
          category: data.category,
          touchpoint: data.touchpoint,
          'connection-country': status && status.status === 'connected' ? status.country : null
        };

        sdk.reporting.analytics('elite', data.category, data.touchpoint);
        sdk.reporting.internal(event).then(() => {
          return resolve();
        });
      } catch (e) {
        return resolve();
      }
    });
  }
}

export default Elite;
