import sdk from '@common-shared/SDK/';

class DnsPrefetch {
  constructor() {
    let action = '';
    if (sdk.proxy.status.status === 'connected') {
      action = 'enable';
    } else {
      action = 'disable';
    }

    this[action]().then(() => {
      sdk.proxy.onStatusChanged.add(this.proxyStatusChanged.bind(this));
    });
  }

  proxyStatusChanged() {
    if (sdk.proxy.status.status === 'connected' && this.status === 'off') {
      this.enable();
    } else if (sdk.proxy.status.status === 'disconnected' && this.status === 'on') {
      this.disable();
    }
  }

  init() {
    browser.privacy.network.networkPredictionEnabled.get({}, response => {
      if (response && response.value) this.status = 'on';
      else this.status = 'off';
    });
  }

  enable() {
    return new Promise(resolve => {
      browser.privacy.network.networkPredictionEnabled.set({ value: false }, () => {
        this.status = 'on';
        return resolve();
      });
    });
  }

  disable() {
    return new Promise(resolve => {
      browser.privacy.network.networkPredictionEnabled.set({ value: true }, () => {
        this.status = 'off';
        sdk?.speed?.reset();
        return resolve();
      });
    });
  }
}

export default DnsPrefetch;
