import sdk from '@common-shared/SDK';
import whiteListDomains from 'shared/config/whiteListDomains';
import Servers from './Servers';
import { isEdge, isFirefox } from '../BrowserDetection';

const PROXY_LOCAL = 'Proxy.proxyLocal';

class Core {
  constructor() {
    this.servers = new Servers();
  }

  updateProxy = async () => {
    const servers = this.servers.getServers(this.status.location);
    await this.servers.setActive(servers);
    return this.render();
  };

  firefoxProxy = direct => {
    if (browser.proxy.onRequest.hasListener(this.firefoxProxyRequestHandler)) {
      browser.proxy.onRequest.removeListener(this.firefoxProxyRequestHandler);
    }
    browser.proxy.onRequest.addListener(this.firefoxProxyRequestHandler, { urls: ['<all_urls>'] });
  };

  firefoxProxyRequestHandler = details => {
    const isConnected = sdk.proxy.status.status === 'connected';
    const { host } = new URL(details.url);
    if (!isConnected) {
      const securedWebsites = Object.keys(sdk.proxy?.secured?.get() || {});
      if (securedWebsites.find(domain => host.indexOf(domain) >= 0)) {
        return this.servers.proxyOptimalPool;
      }
      return { type: 'direct' };
    }
    if (whiteListDomains.includes(host)) return { type: 'direct' };
    if (sdk.proxy.bypass.isInList(host)) return { type: 'direct' };
    return this.servers.proxyConfigPool;
  };

  renderClearProxy = () => this.setPacScript("function FindProxyForURL(url, host) { return 'DIRECT'; }");

  render = direct => {
    if (isFirefox) {
      return this.firefoxProxy(direct);
    }
    return this.pacScriptProxy(direct);
  };

  pacScriptProxy = direct => {
    const proxyLocal = sdk.storage.get(PROXY_LOCAL);
    const isConnected = sdk.proxy.status.status === 'connected';
    let proxyLocalCheck = '';
    if (proxyLocal && isConnected) {
      proxyLocalCheck = `
      var ip = dnsResolve(host);
      if(
        isPlainHostName(host) 
        || shExpMatch(host, '*.local') 
        || isInNet(ip, '10.0.0.0', '255.0.0.0') 
        || isInNet(ip, '172.16.0.0',  '255.240.0.0') 
        || isInNet(ip, '192.168.0.0',  '255.255.0.0') 
        || isInNet(ip, '173.37.0.0',  '255.255.0.0') 
        || isInNet(ip, '127.0.0.0', '255.255.255.0')
       ) return 'DIRECT';
      `;
    }

    const pacScript = `
      let active  = false,
       created = ${Date.now()},
       started = Date.now();
       if((started-100) < created) { active = true; }

      function FindProxyForURL(url, host) {
        if(!url.match(/^(https?)|(ftp):\\/\\//)) return 'DIRECT';
        if(!active && (Date.now() > (started + 2000))) active = true;
        if(!active) return 'DIRECT';
        
        url = url.toLowerCase();
        host = host.toLowerCase();
        
        ${proxyLocalCheck}
        
        ${this.whiteList()}
        
        ${this.bypassWebsitesRow()}
        
        ${this.globalRow(direct, isConnected)}
        
        ${this.securedWebsitesRow(direct)}  
        
        return 'DIRECT';
      }
      `;

    return this.setPacScript(pacScript);
  };

  globalRow = (direct, isConnected) => {
    if (!isConnected) return '';
    return `return '${this.servers.concat(this.servers.getActive()) + (direct ? 'DIRECT;' : '')}';\n`;
  };

  bypassWebsitesRow = () => {
    const bypass = sdk.proxy?.bypass?.get();
    if (!bypass?.length) return '';
    const bypassList = bypass
      .flatMap(domain => [`host == '${domain}'`, `dnsDomainIs(host, '.${domain}')`])
      .join(' || ');
    return `if(${bypassList}) return 'DIRECT';`;
  };

  whiteList = () => {
    const condition = ['www.google-analytics.com', 'localhost', ...whiteListDomains]
      .flatMap(domain => [`host == '${domain}'`, `dnsDomainIs(host, '.${domain}')`])
      .join(' || ');
    return `if (host == '127.0.0.1' || ${condition}) return 'DIRECT';`;
  };

  securedWebsitesRow = direct => {
    const securedWebsites = sdk.proxy?.secured?.get() || {};
    if (Object.keys(securedWebsites).length === 0) return '';
    const domains = Object.keys(securedWebsites);

    let servers = this.servers.getActive();
    if (!servers?.length) {
      servers = this.servers.getServers();
    }
    const secured = this.servers.concat(servers);
    const condition = domains
      .flatMap(domain => [`host == '${domain}'`, `dnsDomainIs(host, '.${domain}')`])
      .join(' || ');
    return `if(${condition}) return '${secured}${direct ? 'DIRECT;' : ''}';`;
  };

  setPacScript = pacScript =>
    new Promise(resolve => {
      debug(pacScript);
      debug('-----------------------------');

      const config = {
        mode: 'pac_script',
        pacScript: {
          data: pacScript
        }
      };

      browser.proxy.settings.set({ value: config, scope: 'regular' }, () => {
        resolve();
      });

      browser.extension.isAllowedIncognitoAccess(result => {
        if (!result) return;

        try {
          browser.proxy.settings.set({ value: config, scope: 'incognito_persistent' }, () => {});
        } catch (e) {
          error(e);
        }
      });
    });
}

export default Core;
