import sdk from '@common-shared/SDK/';

const COUNTER = 'Connection.Sites.counter';
const SAVE_INTERVAL = 30 * 1000;

class SitesCounter {
  constructor() {
    this.counter = sdk.storage.get(COUNTER) ? sdk.storage.get(COUNTER) : 0;

    this.save();

    sdk.tabs.onPageView.add(() => {
      if (sdk.proxy.status.status === 'connected') this.counter++;
    });
  }

  getCounter() {
    return this.counter;
  }

  save() {
    if (!this.saveInterval) {
      this.saveInterval = setInterval(this.save.bind(this), SAVE_INTERVAL);
      return;
    }

    sdk.storage.set(COUNTER, this.counter);
  }
}

export default SitesCounter;
