import sdk from '@common-shared/SDK/';
import Rules from './Rules';

import DomMatcher from './DomMatcher';
import RequestMatcher from './RequestMatcher';

const STATUS = 'AdBlocker.status';
const COUNTER = 'AdBlocker.counter';

class AdBlocker {
  constructor() {
    this.rules = new Rules();

    this.rules.getRules().then(rules => {
      const [requestRules, domRules] = rules;
      const temp = [];
      for (const k in requestRules.filters) {
        const r = requestRules.filters[k];

        if (Object.keys(r).length > 2) continue;
        temp.push(r.filter.toString());
      }

      window.tempFilters = temp;

      this.status = sdk.storage.get(STATUS) ? sdk.storage.get(STATUS) : 'off';
      this.counter = sdk.storage.get(COUNTER) ? sdk.storage.get(COUNTER) : 0;

      this.domMatcher = new DomMatcher(domRules);
      this.requestMatcher = new RequestMatcher(requestRules);
    });
  }

  check(details) {
    if (this.status !== 'on' || details.type === 'main_frame') return false;

    if (details.type === 'sub_frame') details.type = 'subdocument';

    const request = {
      url: details.url,
      type: details.type.toLowerCase(),
      referer: details.url.match(/https?:\/\/([^/]+)/)[1],
      thirdParty: -1
    };

    for (const k in details.requestHeaders) {
      const header = details.requestHeaders[k];
      if (header.name.toLowerCase() === 'referer') {
        request.referer = header.value.match(/https?:\/\/([^/]+)/)[1];
      }
    }

    const tab = sdk.tabs.getByTabId(details.tabId);
    if (tab && tab.domain !== request.referer) request.thirdParty = 1;

    request.tab = tab;

    const shouldBeBlocked = this.requestMatcher.check(request);
    if (shouldBeBlocked) this.counter++;

    return shouldBeBlocked;
  }

  getCounter() {
    return this.counter;
  }

  getStatus() {
    return this.status;
  }

  getDomRules(id) {
    if (this.status !== 'on' || !this.isConnected(id)) return false;

    return this.domMatcher.getRules();
  }

  isConnected(id) {
    if (!id) return false;

    const tab = sdk.tabs.getByTabId(id);
    if (!tab) return false;

    if (sdk.proxy.bypass.isInList(tab.domain)) return false;
    if (sdk.proxy.secured.isInList(tab.domain)) return true;
    if (sdk.proxy.status.status === 'connected') return true;

    return false;
  }

  setStatus(status) {
    if (['on', 'off', 'disabled'].indexOf(status) === -1) return;
    this.status = status;
    return sdk.storage.set(STATUS, status);
  }
}

export default AdBlocker;
