import sdk from '@common-shared/SDK';
import Signal from '../Wdgt/Signal';

import Core from './Core';
import { isEdge, isFirefox } from '../BrowserDetection';

const STATUS = 'Proxy.Status.status';

const ERROR_LIMIT = 50;
const ON_PROXY_ERROR_RECALL = 5 * 1000;

class Status extends Core {
  constructor() {
    super();

    this.status = sdk.storage.get(STATUS) ? sdk.storage.get(STATUS) : { status: 'disconnected', location: 'optimal' };

    this.createSignals();
    this.watchForProxyError();
  }

  createSignals() {
    this.onProxyError = new Signal();
    this.onStatusChanged = new Signal();
  }

  updateStatus(status, location) {
    this.status.status = status;
    this.status.location = location || this.status.location;

    return sdk.storage.set(STATUS, this.status);
  }

  getGlobalStatus = () => 'avaliable';

  watchForProxyError() {
    let lastError;
    let errorCounter = 0;
    let waitForRecall = false;

    const handler = error => {
      if (sdk.proxy.disableErrors || waitForRecall) return;

      waitForRecall = true;

      errorCounter = lastError && lastError < Date.now() - 30 * 1000 ? 0 : ++errorCounter;
      lastError = Date.now();

      if (errorCounter === ERROR_LIMIT) {
        return sdk.proxy.disconnect({ error }).then(() => {
          this.onProxyError.dispatch('disconnect');
          errorCounter = 0;
          waitForRecall = false;
        });
      }

      this.onProxyError.dispatch('update');
      this.render(true);

      setTimeout(() => {
        waitForRecall = false;
        this.render();
      }, ON_PROXY_ERROR_RECALL);
    };

    if (isFirefox) {
      browser.proxy.onError.addListener(handler);
    } else {
      browser.proxy.onProxyError.addListener(handler);
    }
  }
}

export default Status;
