import { PANEL_ANIMATED, PANEL_LOADED, STORAGE_ERROR, TOOLTIPS_SHOWED } from '@common-shared/Utils/StorageKeys';
import sdk from '@common-shared/SDK/';

class Storage {
  constructor() {
    this.data = {};
  }

  init() {
    console.log(sdk);
    return new Promise(resolve => {
      sdk.storage = {};

      sdk.storage.get = this.get.bind(this);
      sdk.storage.set = this.set.bind(this);
      sdk.storage.print = () => {
        log(this.data);
      };
      sdk.storage.clean = () => {
        browser.storage.local.clear();
        location.reload();
      };

      try {
        browser.storage.local.get(null, data => {
          debug(data);

          this.data = data;
          this.data[PANEL_LOADED] = false;
          this.data[PANEL_ANIMATED] = false;

          resolve();
        });
      } catch (e) {
        this.data = {};
        this.data[PANEL_LOADED] = false;
        this.data[PANEL_ANIMATED] = false;
        this.data[STORAGE_ERROR] = true;
        this.data[TOOLTIPS_SHOWED] = true;

        resolve();
      }
    });
  }

  get(key) {
    if (typeof key === 'object') {
      const result = [];
      for (const k in key) {
        result.push(this.data[key[k]]);
      }

      return result;
    }

    return this.data[key];
  }

  set(key, value) {
    this.data[key] = this.clone(value);
    if (!value) {
      return new Promise(resolve => {
        try {
          browser.storage.local.remove(key, () => {
            resolve();
          });
        } catch (e) {
          resolve();
        }
      });
    }

    return new Promise(resolve => {
      try {
        browser.storage.local.set(
          {
            [key]: this.data[key]
          },
          () => {
            resolve();
          }
        );
      } catch (e) {
        resolve();
      }
    });
  }

  clone(value) {
    if (typeof value !== 'object') return value;
    return JSON.parse(JSON.stringify(value));
  }
}

export default Storage;
