import sdk from '@common-shared/SDK';
import { isLooksLikeDomain } from '@common-shared/Utils/DomainUtils';
import isNumber from 'lodash/isNumber';

function isValidServerConfig({ host, port }) {
  if (!isLooksLikeDomain(host)) return false;
  if (!isNumber(port)) return false;
  if (port < 0 || port > 65353) return false;
  return true;
}

class HssConfig {
  static async loadConfig() {
    const [elite, regular] = await Promise.all([sdk.getEliteSD(), sdk.getFreeSD()]);
    return HssConfig.parseConfig(elite, regular);
  }

  static parseConfig(elite, regular) {
    const config = {
      servers: {
        elite: {},
        regular: {}
      },
      locations: {
        elite: [],
        regular: []
      }
    };

    try {
      Object.entries(elite).forEach(([country, servers]) => {
        country = country.toLowerCase();
        config.servers.elite[country] = servers.filter(isValidServerConfig).map(({ host, priority, port }) => ({
          host,
          address: host,
          scheme: 'https',
          country,
          priority,
          port
        }));
        config.locations.elite.push(country);
      });
    } catch (e) {
      console.error('HssConfig.parseConfig: unable to parse elite servers', e);
    }

    try {
      Object.entries(regular).forEach(([country, servers]) => {
        country = country.toLowerCase();
        config.servers.regular[country] = servers.filter(isValidServerConfig).map(({ host, priority, port }) => ({
          host,
          address: host,
          scheme: 'https',
          country,
          priority,
          port
        }));
        config.locations.regular.push(country);
      });
    } catch (e) {
      console.error('HssConfig.parseConfig: unable to parse free servers', e);
    }
    return config;
  }
}

export default HssConfig;
