import sdk from '@common-shared/SDK/';

import Rules from './Rules';
import Methods from './Methods';

const REPLACE = 'Sword.Replace';
const ENABLED = 'Sword.Enabled';

const DEFAULT_REPLACE = ['hotspotshield.com', 'betternet.co', 'hamsterdance.com', 'html5zombo.com'];

class Sword {
  constructor() {
    this.tabs = {};
    this.done = false;
    this.enabled = false;
    this.replace = false;
    this.swordQ = {};

    if (!sdk.storage.get(REPLACE)) {
      sdk.storage.set(REPLACE, DEFAULT_REPLACE);
    }

    this.rules = new Rules();
    this.rules
      .loadRules()
      .then(() => {
        this.done = true;
        this.enabled = sdk.storage.get(ENABLED) ? sdk.storage.get(ENABLED) : false;
        this.replace = sdk.storage.get(REPLACE) && sdk.storage.get(REPLACE).length > 0 ? sdk.storage.get(REPLACE) : [];

        if (this.replace.length === 0) {
          this.enabled = false;
          sdk.storage.set(ENABLED, this.enabled);
        }
      })
      .catch(() => {
        this.disable();
      });
  }

  getList() {
    return this.replace;
  }

  removeFromList(k) {
    this.replace.splice(k, 1);
    return sdk.storage.set(REPLACE, this.replace).then(() => {
      this.replace = sdk.storage.get(REPLACE) && sdk.storage.get(REPLACE).length > 0 ? sdk.storage.get(REPLACE) : [];

      if (this.replace.length === 0) {
        this.enabled = false;
        sdk.storage.set(ENABLED, this.enabled);
      }

      return this.replace;
    });
  }

  addToList(domain) {
    let exist = false;
    for (const k in this.replace) {
      if (this.replace[k] === domain) exist = true;
    }

    return new Promise(resolve => {
      if (exist) return resolve();
      this.replace.push(domain);
      sdk.storage.set(REPLACE, this.replace).then(() => {
        return resolve(this.replace);
      });
    });
  }

  message(request, sender) {
    switch (request.method) {
      case 'getSwordData':
        return this.getSwordData(sender.tab.id);
      case 'checkSwordInsertionDomain':
        return this.checkSwordInsertionDomain(request.message.url, sender.tab.id);
    }
  }

  getSwordData(id) {
    if (!this.done || !this.enabled || !this.tabs[id]) return { disabled: true };

    return this.tabs[id];
  }

  checkSwordInsertionDomain(url, id) {
    if (!this.done || !this.enabled || !this.tabs[id]) return { isInList: false };

    const domain = Methods.getDomain(url);
    const fullDomain = Methods.getFullDomain(url);

    if (domain === this.tabs[id].domain) return { isInList: false };

    if (!this.rules.isInList(fullDomain) && !this.rules.isInList(domain)) return { isInList: false };

    return { isInList: true };
  }

  onBeforeRequest(details) {
    if (!this.done || !this.enabled) return false;

    if (details.type === 'main_frame') {
      const replace = this.replace[Math.floor(Math.random() * this.replace.length)];

      this.tabs[details.tabId] = {
        id: details.tabId,
        url: details.url,
        domain: Methods.getDomain(details.url),
        fullDomain: Methods.getFullDomain(details.url),
        aliasDomain: replace
      };
      return false;
    }

    const request = this.getRequestInfo(details);
    if (!request || request.request.url.indexOf(request.tab.domain) === -1) return false;
    if (!this.rules.isInList(request.request.fullDomain) && !this.rules.isInList(request.request.domain)) return false;

    const newUrl = request.request.url.replace(new RegExp(request.tab.domain, 'g'), request.aliasDomain);

    this.swordQ[newUrl] = 'replace';

    return newUrl;
  }

  onBeforeSendHeaders(details) {
    if (!this.done || !this.enabled) return false;

    const request = this.getRequestInfo(details);
    if (!request) return false;
    if (!this.rules.isInList(request.request.fullDomain) && !this.rules.isInList(request.request.domain)) return false;

    const headers = [];
    let updated = false;

    for (const k in request.headers) {
      const header = request.headers[k];

      if (header.value.indexOf(request.tab.domain) !== -1) {
        updated = true;
        header.value = header.value.replace(new RegExp(request.tab.domain, 'g'), request.aliasDomain);
      }

      headers.push(header);
    }

    if (updated) {
      if (details.url) this.swordQ[details.url] = 'update';
      return headers;
    }

    return false;
  }

  onHeadersReceived(details) {
    if (!this.done || !this.enabled) return false;

    const request = this.getRequestInfo(details);
    if (!request || request.request.domain === request.request.initiatorDomain) return false;
    delete this.swordQ[details.url];

    const headers = [];
    let cors = false;
    let updated = false;

    for (const k in request.headers) {
      const header = request.headers[k];

      if (header.name.toLowerCase() === 'access-control-allow-origin') cors = true;
      if (header.value.indexOf(request.aliasDomain) !== -1) {
        updated = true;
        header.value = header.value.replace(new RegExp(request.aliasDomain, 'g'), request.tab.domain);
      }

      headers.push(header);
    }

    if (!cors) {
      updated = true;
      headers.push({
        name: 'Access-Control-Allow-Origin',
        value: request.request.initiator
      });
    }

    if (updated) {
      return headers;
    }

    return false;
  }

  getStatus() {
    return this.enabled ? 'on' : 'off';
  }

  setStatus(status) {
    if (['on', 'off', 'disabled'].indexOf(status) === -1) return;

    this.enabled = status === 'on';
    if (this.replace.length === 0) {
      this.enabled = false;
    }
    return sdk.storage.set(ENABLED, this.enabled);
  }

  getRequestInfo(r) {
    if (!r || !r.tabId || !this.tabs[r.tabId]) return false;
    const request = {
      tab: {
        url: this.tabs[r.tabId].url,
        domain: this.tabs[r.tabId].domain,
        fullDomain: this.tabs[r.tabId].fullDomain
      },
      request: {
        url: r.url,
        domain: Methods.getDomain(r.url),
        initiator: r.initiator,
        initiatorDomain: Methods.getDomain(r.initiator),
        fullDomain: Methods.getFullDomain(r.url)
      },
      headers: r.requestHeaders ? r.requestHeaders : r.responseHeaders ? r.responseHeaders : [],
      aliasDomain: this.tabs[r.tabId].aliasDomain
    };

    if (request.tab.domain === request.request.domain) return false;

    return request;
  }
}

export default Sword;
