import sdk from '@common-shared/SDK/';

const COUNTER = 'Connection.Bandwidth.counter';
const SAVE_INTERVAL = 60 * 1000;

class BandwidthCounter {
  constructor() {
    this.restore();
    this.save();
  }

  getCounter() {
    return this.counter;
  }

  save() {
    if (!this.saveInterval) {
      this.saveInterval = setInterval(this.save.bind(this), SAVE_INTERVAL);
      return;
    }

    sdk.storage.set(COUNTER, this.counter);
  }

  count(headers) {
    if (!this.counter) return;

    for (const k in headers) {
      const header = headers[k];
      header.name = header.name.toLowerCase();
      header.value = header.value.toLowerCase();

      if (header.name === 'content-length') {
        this.update(header.value);
        return { cancel: false };
      }
    }
  }

  update(number) {
    number = Number((Number(number) / 1048576).toFixed(4));

    const now = Math.round(Date.now() / 1000);
    const day = 24 * 60 * 60;
    const week = 7 * day;
    const month = 30 * day;
    const c = this.counter;

    if (now < c.day.end) c.day.bandwidth += number;
    else c.day = { bandwidth: 0, end: now + day };

    if (now < c.week.end) c.week.bandwidth += number;
    else c.week = { bandwidth: 0, end: now + week };

    if (now < c.month.end) c.month.bandwidth += number;
    else c.month = { bandwidth: 0, end: now + month };

    c.total.bandwidth += number;

    this.counter = c;
  }

  restore() {
    let counter = sdk.storage.get(COUNTER);
    if (!counter) {
      const now = Math.round(Date.now() / 1000);
      const day = 24 * 60 * 60;
      const week = 7 * day;
      const month = 30 * day;

      counter = {
        total: {
          bandwidth: 0
        },
        month: {
          bandwidth: 0,
          end: now + month
        },
        week: {
          bandwidth: 0,
          end: now + week
        },
        day: {
          bandwidth: 0,
          end: now + day
        }
      };

      sdk.storage.set(COUNTER, counter);
    }

    this.counter = counter;
  }
}

export default BandwidthCounter;
