import lodash from 'lodash';
import sdk from '@common-shared/SDK';
import ContentWall from '../ContentWall';

class Messaging {
  constructor() {
    this.contentWall = new ContentWall();

    browser.runtime.onMessage.addListener((request, sender, callback) => {
      this.handleRequest(request, callback);

      return true;
    });
  }

  async handleRequest(request, callback) {
    if (!request || !request.method) return;

    if (request.method) {
      const resource = lodash.get(sdk, request.method);
      let response = null;
      if (typeof resource === 'function') {
        response = await resource(...request.arguments);
      }
      callback(response);
    }

    if (request.from === 'ContentWall') {
      this.contentWall.handleRequest(request).then(result => {
        callback(result);
      });
    } else if (request.from === 'AdBlocker') {
      callback(sdk.adBlocker?.getDomRules());
    }
  }
}

export default Messaging;
