import sdk from '@common-shared/SDK';
import Config from './Config';
import { isFirefox } from '../BrowserDetection';

const ACTIVE = 'Proxy.Servers.active';
const LIMIT = 5;

class Servers {
  constructor() {
    this.config = new Config();
    sdk.servers = this;
    this.active = sdk.storage.get(ACTIVE) ? sdk.storage.get(ACTIVE) : [];
  }

  setActive(active) {
    this.active = active;
    if (isFirefox) {
      this.proxyConfigPool = this.active.map(({ address, port, scheme }) => ({
        port,
        type: scheme,
        host: address,
        failoverTimeout: 3
      }));
      this.proxyOptimalPool = this.getServers().map(({ address, port, scheme }) => ({
        port,
        type: scheme,
        host: address,
        failoverTimeout: 3
      }));
    }
    return sdk.storage.set(ACTIVE, active);
  }

  getActive = () => this.active;

  getServers(location = 'us', type = sdk.user?.status?.elite ? 'elite' : 'regular') {
    const servers = this.getServersByPriority(this.config.getServers(type)[location]).splice(0, LIMIT);
    if (servers.length < LIMIT) {
      const backup = this.getBackupServers(location, type);

      let i = servers.length;
      while (i < LIMIT) {
        if (backup[i]) {
          servers.push(backup[i]);
        }
        i++;
      }
    }

    return servers;
  }

  getBackupServers(location, type) {
    const temp = this.config.getServers(type);
    const servers = [];

    for (const k in temp) {
      if (k === location) continue;

      for (const i in temp[k]) {
        if (!temp[k][i].disable) {
          servers.push(temp[k][i]);
        }
      }
    }

    return this.getServersByPriority(servers);
  }

  getServersByPriority(servers, result) {
    const priorities = [];
    let total = 0;
    result = result || [];

    for (const k in servers) {
      total += servers[k].priority;
      priorities.push(total);
    }

    const rand = Math.floor(Math.random() * total);

    for (let i = 0; i < priorities.length; i++) {
      if (rand <= priorities[i]) {
        result.push(servers[i]);
        servers.splice(i, 1);
        return this.getServersByPriority(servers, result);
      }
    }

    return result;
  }

  getOptimalLocation(type) {
    // @todo choose server based on network speed
    const locations = this.config.getLocations(type);
    return locations[[Math.floor(Math.random() * locations.length)]];
  }

  disableServer(address) {
    if (this.config.disableServer(address)) {
      sdk.proxy.updateProxy();
    }
  }

  concat(servers) {
    let row = '';

    for (const k in servers) {
      if (!servers[k]) continue;
      const scheme = servers[k].scheme === 'http' ? 'proxy' : servers[k].scheme;
      row += `${scheme} ${servers[k].address}:${servers[k].port};`;
    }

    return row;
  }
}

export default Servers;
